import './payment.scss'

import Card1 from '../../assets/images/icons/credit.svg'
import Card2 from '../../assets/images/icons/paypal.svg'
import Card3 from '../../assets/images/icons/cash.svg';

import {
    Paypal,
    Cash,
    Credit
} from '../../assets/icons/icons'

import Titles from '../../components/titles/titles'

const Payment = ({ isOpened, setPaymentState  }) => {
    return (
        <div className={`payment ${isOpened ? 'opened' : ''}`}>
            <div className="payment-top">
                <Titles title="Payment" date="3 payment method available" />
            </div>

            <div className="payment-method">
                <h3 className="title">
                    Payment Method
                </h3>

                <div className="cards">
                    <div className="credit-card">
                        <input value="card" name="card" id="credit" type="radio" />
                        <label htmlFor="credit" className="label">
                            {/* <img src={<Credit/>} className="label-img" alt="" /> */}
                            <svg className="lebel-img" width="25" height="24" viewBox="0 0 25 24" xmlns="http://www.w3.org/2000/svg">
                                <path fill-rule="evenodd" clip-rule="evenodd" d="M1.75 9C1.75 6.37665 3.87665 4.25 6.5 4.25H18.5C21.1234 4.25 23.25 6.37665 23.25 9V15C23.25 17.6234 21.1234 19.75 18.5 19.75H6.5C3.87665 19.75 1.75 17.6234 1.75 15V9ZM3.33697 8.25H21.663C21.3245 6.81665 20.0368 5.75 18.5 5.75H6.5C4.96321 5.75 3.67555 6.81665 3.33697 8.25ZM21.75 9.75H3.25V15C3.25 16.7949 4.70507 18.25 6.5 18.25H18.5C20.2949 18.25 21.75 16.7949 21.75 15V9.75ZM13.75 15C13.75 14.5858 14.0858 14.25 14.5 14.25H18.5C18.9142 14.25 19.25 14.5858 19.25 15C19.25 15.4142 18.9142 15.75 18.5 15.75H14.5C14.0858 15.75 13.75 15.4142 13.75 15Z" />
                            </svg>
                            <h3 className="label__title">
                                Credit Card
                            </h3>
                        </label>
                    </div>

                    <div className="paypal">
                        <input value="card" id="paypal" name="card" type="radio" />
                        <label htmlFor="paypal" className="label">
                            <img src={Card2} className="label-img" alt="" />
                            {/* <svg width="19" height="20" viewBox="0 0 19 20"  xmlns="http://www.w3.org/2000/svg">
                                <path d="M15 5.2C16.7 6.2 17.5 8 17.5 10C17.5 12.5 15 14.5 12.5 14.5H9.9L9.3 18.1C9.25325 18.3293 9.12758 18.5349 8.94486 18.6811C8.76214 18.8272 8.53395 18.9047 8.3 18.9H5.6C5.52501 18.9015 5.45064 18.8861 5.38239 18.855C5.31415 18.8239 5.25378 18.7778 5.20577 18.7202C5.15775 18.6626 5.12331 18.5949 5.105 18.5222C5.08669 18.4494 5.08498 18.3735 5.1 18.3L5.3 16.9M7.5 11H10C12.5 11 15 8.5 15 6C15 3 13.1 1 10 1H4.5C4 1 3.5 1.5 3.5 2L1.5 16C1.5 16.5 2 17 2.5 17H5.3L6.5 12C6.6 11.4 6.9 11 7.5 11Z" stroke="#ABBBC2" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                            </svg> */}
                            <h3 className="label__title">
                                Credit Card
                            </h3>
                        </label>
                    </div>

                    <div className="cash">
                        <input value="card" id="cash" name="card" type="radio" />
                        <label htmlFor="cash" className="label">
                            {/* <img src={Card3} className="label-img" alt="" /> */}
                            <svg width="21" height="20" viewBox="0 0 21 20" xmlns="http://www.w3.org/2000/svg">
                                <path d="M14.688 0.5C17.8978 0.5 20.5 3.16116 20.5 6.44374V13.5563C20.5 16.8388 17.8978 19.5 14.688 19.5H6.31204C3.10219 19.5 0.5 16.8388 0.5 13.5563V6.44374C0.5 3.16116 3.10219 0.5 6.31204 0.5H14.688ZM14.688 1.98651H6.31204C3.90498 1.98651 1.95358 3.98213 1.95358 6.44374V13.5563C1.95358 16.0179 3.90498 18.0135 6.31204 18.0135H14.688C17.095 18.0135 19.0464 16.0179 19.0464 13.5563L19.046 13.279L15.8499 13.2798C14.0084 13.2787 12.5159 11.7531 12.5147 9.86949C12.5147 8.04914 13.9101 6.56244 15.6673 6.46431L15.8504 6.45916L19.046 6.459L19.0464 6.44374C19.0464 4.05454 17.2081 2.10431 14.8991 1.99165L14.688 1.98651ZM19.046 7.945L15.8508 7.94567C14.8109 7.94632 13.9683 8.80743 13.9683 9.86904C13.9689 10.8811 14.7329 11.7102 15.7033 11.7874L15.8504 11.7933L19.046 11.793V7.945ZM16.2942 9.06518C16.6956 9.06518 17.021 9.39795 17.021 9.80844C17.021 10.1847 16.7475 10.4957 16.3928 10.5449L16.2942 10.5517H15.9921C15.5907 10.5517 15.2653 10.2189 15.2653 9.80844C15.2653 9.43215 15.5388 9.12118 15.8935 9.07196L15.9921 9.06518H16.2942ZM10.8539 4.99736C11.2553 4.99736 11.5807 5.33013 11.5807 5.74062C11.5807 6.1169 11.3073 6.42788 10.9525 6.47709L10.8539 6.48388H5.62203C5.22063 6.48388 4.89524 6.15111 4.89524 5.74062C4.89524 5.36434 5.16866 5.05337 5.5234 5.00415L5.62203 4.99736H10.8539Z" />
                            </svg>
                            <h3 className="label__title">
                                Credit Card
                            </h3>
                        </label>
                    </div>
                </div>

                <div className="private-info">
                    <div className="private">
                        <label htmlFor="name-input" className="name-label">Cardholder Name</label>
                        <input type="text" placeholder="Levi Ackerman" id="name-input" />
                    </div>

                    <div className="private">
                        <label htmlFor="name" className="name-label">Card Number</label>
                        <input type="number" placeholder="2564 1421 0897 1244" id="name" />
                    </div>

                    <div className="wrapper">
                        <div className="left">
                            <label htmlFor="date" className="name-label">Expiration Date</label>
                            <input type="date" placeholder="Levi Ackerman" id="date" />
                        </div>
                        <div className="right">
                            <label htmlFor="password" className="name-label">CVV</label>
                            <input type="password" placeholder=". . ." id="password" />
                        </div>
                    </div>

                    <div className="order-type">
                        <div className="left">
                            <label htmlFor="select" className="name-label">Order Type</label>
                            <select id="select" className="homepage-left-main-top-right__select">
                                <option value="Dine In" className="option">Dine In</option>
                                <option value="balo" className="option">Balo</option>
                                <option value="battar" className="option">Battar</option>
                            </select>
                        </div>

                        <div className="right">
                            <label htmlFor="name" className="name-label">Card Number</label>
                            <input type="number" placeholder="140" id="name" />
                        </div>
                    </div>

                    <div className="buttons">
                        <button className="cancel"  onClick={() => setPaymentState(!isOpened)}>Cancel</button>
                        <button className="confirm">Confirm Payment</button>
                    </div>

                </div>

            </div>
        </div>
    )
}

export default Payment;