import './settingsBlock.scss'

const SettingsBlock = ({img,title,text}) => {
    return (
        <div className="block-settings">
            <img src={img} alt="" className="block-img"/>

            <div className="block-right">
                <h4 className="block-title">{title}</h4>
                <h5 className="block-text">{text}</h5>
            </div>
        </div>
    )
}

export default SettingsBlock;