import { Link } from 'react-router-dom'
import Logo from '../../assets/images/icons/Logo.svg'

import {
    Home,
    Discount,
    Graph,
    Message,
    Notification,
    Settings,
    Exit
} from '../../assets/icons/icons'

import SideBarBtn from '../../components/saidbarbtn/saidbarbtn'
import './sidebar.scss'

const Sidebar = ({activePage, setActivePage}) => {
    return (
        <div className="sidebar">

            <div className="logo-holder">
                <Link to="/" className="logo-holder-link">
                    <img src={Logo} alt="" className="logo-img"/>
                </Link>
            </div>

            <Link to="/HomePage">
                <SideBarBtn
                    icon={<Home/>}
                    active={activePage == 'HomePage'}
                    onClick = {() => setActivePage('HomePage')}
                />
            </Link>

            <Link to="/Discount">
                <SideBarBtn
                    icon={<Discount/>}
                    active={activePage == 'DisCount'}
                    onClick = {() => setActivePage('DisCount')}
                />
            </Link>

            <Link to="/Graph">
                <SideBarBtn
                    icon={<Graph/>}
                    active={activePage == 'Graph'}
                    onClick = {() => setActivePage('Graph')}
                />
            </Link>

            <Link to="/Message">
                <SideBarBtn
                    icon={<Message/>}
                    active={activePage == 'Message'}
                    onClick = {() => setActivePage('Message')}
                />
            </Link>

            <Link to="/Notification">
                <SideBarBtn
                    icon={<Notification/>}
                    active={activePage == 'Notification'}
                    onClick = {() => setActivePage('Notification')}
                />
            </Link>

            <Link to="/Settings">
                <SideBarBtn
                    icon={<Settings/>}
                    active={activePage == 'Settings'}
                    onClick = {() => setActivePage('Settings')}
                />
            </Link>

            <Link to="/Exit">
                <SideBarBtn
                    icon={<Exit/>}
                    active={activePage == 'Exit'}
                    onClick = {() => setActivePage('Exit')}
                />
            </Link>



        </div>
    )
}

export default Sidebar;