import './absolute.scss'

const Absolute = ({ paymentState, setPaymentState }) => {
    return(
        <div className={`absolute `}>
                    <div className="absolute-top">
                        <h3 className="absolute-top__title">
                            Discount
                        </h3>
                        <h4 className="absolute-top__price">
                            $0
                        </h4>
                    </div>
                    <div className="absolute-center">
                        <h4 className="absolute-center__title">
                            Sub total
                        </h4>

                        <h4 className="absolute-center__price">
                            $21.03
                        </h4>
                    </div>

                    <button onClick={() => setPaymentState(!paymentState)}  className="absolute-btn">Continue to Payment</button>
        </div>
    )
}

export default Absolute;