import './priceCardBlock.scss'

const PriceCardBlock = ({img, status,resultImg,price,title}) => {
    return(
        <div className="price-cards">
            <div className="price-cards-top">
                <div className="img-wrapper">
                    <img src={img} alt=""/>
                </div>
                <h4 className="status-title">
                    {status}
                </h4>
                <img src={resultImg} alt="" className="resultImg"/>
            </div>

            <div className="price-cards-center">
                <h4 className="price-info">
                   {price}
                </h4>
            </div>

            <div className="price-cards-bottom">
                <h4 className="title-card">
                    {title}
                </h4>
            </div>
        </div>
    )
}

export default PriceCardBlock;