import './orderNote.scss'

import Delete from '../../assets/images/icons/Delete.svg'

const OrderNote = ({ foodName, numberOfFood, foodPrice, maxPrice, foodImg }) => {
    return (
        <div className="order-note">
            <div className="order-note-left">
                <div className="order-note-left-top">
                    <div className="alone">
                        <img src={foodImg} className="food-img" alt="" />
                        <div className="food-order-info">
                            <h4 className="food-name">{foodName}</h4>
                            <h4 className="food-price">${foodPrice}</h4>
                        </div>
                    </div>
                    <div className="numberr-food">
                        <h4 className="number-food">{numberOfFood}</h4>
                    </div>
                </div>

                <input type="text" placeholder="Order Note..." className="order-input" />
            </div>

            <div className="order-note-right">
                <h4 className="max-price">${maxPrice}</h4>
                <div className="delete">
                    <img src={Delete} className="delete-img" alt="" />
                </div>
            </div>
        </div>
    )
}

export default OrderNote;