import Edit from '../../assets/images/icons/Edit.svg'

import './editBlock.scss'

const EditBlock = ({ img, title, price, count }) => {
    return (
        <div className="edit-block-main">
            <div className="edit-block-mainn">
                <img src={img} alt="" className="edit-block-main__img" />
                <h4 className="edit-block-main__title">{title}</h4>
                <div className="flexbox">
                    <h4 className="edit-block-main__price">{price}</h4>
                    <h4 className="edit-block-main__count">{count}</h4>
                </div>
                <div className="bottom">
                <img src={Edit} alt="" className="edit-img" />
                <h4 className="smth">Edit dish</h4>
            </div>
            </div>
        </div>



    )
}

export default EditBlock;