import './dishCard.scss'

import { Link } from 'react-router-dom'

const DishCard = ({ img, title, price, subtitle }) => {
    return (
        <Link className="dish-card">
            <div className="d">
                <div className="img-holder"><img src={img} alt="" className="dish-card__img" /></div>
                <div className="ds"><h2 className="dish-card__title">{title}</h2></div>
                <h3 className="dish-card__price">${price}</h3>
                <h4 className="dish-card__subtitle">{subtitle}</h4>
            </div>
        </Link>
    )
}

export default DishCard