import './customerInfo.scss'

const CustomerInfo = ({customerImg, customerName, menu, customerPayment, status}) => {
    return(
        <div className="customer-info">
            <div className="customer">
                <img src={customerImg} alt="" className="customer-img"/>
                <h3 className="customer-name">{customerName}</h3>
            </div>

            <div className="customer-menu">
                <h3 className="customer-menu_title">{menu}</h3>
            </div>

            <div className="customer-payment">
                <h3 className="customer-payment__title">{customerPayment}</h3>
            </div>

            <div className={`customer-status ${status.toLowerCase()}`}>
                <h4 className={`status__title ${status.toLowerCase()}`}>
                    {status}
                </h4>
            </div>
        </div>
    )
}

export default CustomerInfo;