import './foodLists.scss'

const FoodLists = ({foodImg, foodName, ordered}) => {
    return(
        <div className="food-list">
            <img src={foodImg} alt="" className="food-list__img"/>
            <div className="food-list-right">
                <h3 className="food-list__title">
                    {foodName}
                </h3>
                <h3 className="ordered">
                    {ordered}
                </h3>
            </div>
        </div>
    )
}

export default FoodLists;   